import React, { useRef, useEffect, useState } from "react";
import API from "@utilities/API";
// import "./style.css";

function DataLoader() {
	useEffect(() => {
		loadAllLocations();
	});

	const loadAllLocations = () => {
		API.getAllLocations()
			.then(res => {
				console.log(res);
			})
			.catch(err => console.log(err));
	};

	return (
		<>
			<div>DATA LOADER</div>
		</>
	);
}

export default DataLoader;
